.header {
	width: 100%;
	min-width: 1200px;
	margin-bottom: 29px;

	@media (--phone) {
		min-width: 320px;
	}

	&__top {
		padding: 0 15px;
		height: 89px;
		background-color: #005fb2;
	}

	&__inner {
		margin: 0 auto;
		max-width: 1170px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100%;

		&_bottom {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

	&__left {
		display: flex;
		align-items: center;
	}

	&__right {
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}

	&__address {
		margin-left: 63px;
		background: svg-load('header-address.svg', fill="#fff") no-repeat left center;
		padding: 0 0 0 30px;
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;

		&:hover {
			background-image: svg-load('header-address.svg', fill="#ffff00");
			color: #ffff00;
		}

		a {
			text-decoration: none;
			color: #fff;

			&:hover {
				color: #ffff00;
			}
		}
	}

	&__bottom {
		padding: 0 15px;
		height: 48px;
		background-color: #0092d4;
	}
}